}
.video-stub__play {
  display: block;
  background-image: url("../img/play icon.png");
  background-repeat: no-repeat;
  position: relative;
  width: 135px;
  height: 77px;
  z-index: 3;
  left: 28%;
  top: 32%;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .video-stub__play {
    background-image: url("../img/play-icon@2x.png");
  }
}
.video-bg__video-stub {
  display: block;
  background-image: url("../img/video.jpg");
  background-repeat: no-repeat;
  width: 333px;
  height: 194px;
  z-index: 2;
  position: relative;
  cursor: pointer;
  left: 8%;
  top: 12.5%;
}
.block-content__button {
  display: block;
  background-image: url("../img/button bg.png");
  background-repeat: no-repeat;
  width: 400px;
  height: 86px;
  cursor: pointer;
  margin-left: 1.9%;
  margin-top: 1.5%;
}
.block-content__button:hover {
  background-image: url("../img/button bg hover.png");
  background-repeat: no-repeat;
  margin-left: -0.7%;
  margin-top: -0.6%;
  padding-right: 30px;
  padding-bottom: 1.2%;
  margin-left: 0.6%;
  margin-top: 0.47%;
}
.video-bg__video-stub:hover .video-stub__play {
  background-image: url("../img/play icon hover.png");
  background-repeat: no-repeat;
  width: 135px;
  height: 77px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
