.block-content__button h2 {
  position: relative;
  top: 40%;
  font-size: 22px;
  top: 34%;
  left: 14%;
  text-transform: uppercase;
  color: #801715;
  font-weight: bold;
  margin: 0;
  padding: 0;
  width: 300px;
  letter-spacing: 2px;
}
.block-content__button:hover h2 {
  top: 44%;
  left: 16%;
  font-size: 23px;
}
.block-content__logo {
  width: 173px;
  height: 63.3px;
  fill: white;
  margin-left: 11.3%;
  margin-bottom: 3.4%;
  padding-top: 4%;
}
.block-content__slogan {
  margin-left: 2.3%;
}
.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.65);
  cursor: default;
}
.popap {
  display: none;
  position: absolute;
  top: 30%;
  left: 50%;
  z-index: 7;
}
.popap__exit {
  position: absolute;
  margin-left: 10px;
  cursor: pointer;
  width: 33px;
  height: 33px;
}
.popap-show,
.overlay-show {
  display: block;
  animation-duration: 0.55s;
  animation-fill-mode: both;
  animation-name: popapInDown;
}
@keyframes popapInDown {
  0% {
    opacity: 0;
    transform: translate3d(0px, -100%, 0px);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
