    width: 500px;
    height: 106px;
    margin-left: 19%;
    margin-top: 1.5%;
  }
  .block-content__button:hover {
    width: 500px;
    height: 122px;
    margin-left: 18%;
    margin-top: -1%;
  }
  .block-content__button h2 {
